<template>
  <div class="box">
    <div class="content">
      <div class="searchbox">
        <form action="/">
          <van-search
            v-model="value"
            placeholder="请输入搜索关键词"
            show-action
            @search="onSearch"
            @cancel="onCancel"
          />
        </form>
      </div>
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <div class="position-box"></div>
          <van-tabs v-model="active" color="#ff8114" title-active-color="#ff8114">
            <van-tab title="全部">
              <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
              >
                <Kindlist :prolist="prolist" />
              </van-list>
            </van-tab>
            <van-tab title="苹果"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="桃子"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="梨子"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="橙子"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="蓝莓"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="草莓"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="猕猴桃"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="葡萄"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="火龙果"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="橘子"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="柚子"><Kindlist :prolist="prolist" /></van-tab>
            <van-tab title="香蕉"><Kindlist :prolist="prolist" /></van-tab>
          </van-tabs>
      </van-pull-refresh>
      <!-- 购物车和返回顶部 -->
    </div>
    <div class="backtop">
      <div class="icons"><van-icon name="cart-o" size=".24rem" color="#ccc"/></div>
      <div class="icons goTop"><van-icon name="arrow-up" size=".24rem" color="#ccc"/></div>
    </div>
  </div>
</template>
<script>
// import axios from 'axios'
import Vue from 'vue'
import axios from 'axios'
import Kindlist from '@/components/Kindlist'
import { NavBar, Icon, Tab, Tabs, Search, List, Lazyload, PullRefresh } from 'vant'
Vue.use(NavBar)
Vue.use(Icon)
Vue.use(Tab).use(Tabs)
Vue.use(Search)
Vue.use(List)
Vue.use(Lazyload)
Vue.use(PullRefresh)
export default {
  props: {
  },
  data () {
    return {
      active: 0,
      prolist: [],
      Kindlist: [],
      value: '',
      finished: false,
      loading: false,
      isLoading: false
    }
  },
  created () {
    axios.get('/api/pro').then((res) => {
      this.prolist = res.data.data
    })
  },
  components: {
    Kindlist
  },
  methods: {
    getType (index, res) {
      if (index > 0) {
        axios.get('/api/kind').then((res) => {
          this.kindlist = res.data.data
        })
      } else {
        axios.get('/api/pro').then((res) => {
          this.prolist = res.data.data
        })
      }
    },
    onClickLeft () {
      // Toast('返回');
    },
    onClickRight () {
      // Toast('按钮');
    },
    onSearch () {
      this.$router.push('/kind')
    },
    onCancel () {},
    onLoad () {
      console.log('上拉加载开始')
      this.loading = true
      axios.get('/api/pro', {
        params: {
          pageCode: this.pageCode,
          limitNum: 12
        }
      }).then(res => {
        this.loading = false
        this.pageCode++
        if (res.data.code === '10000') {
          this.finished = true
        } else {
          this.prolist = [...this.prolist, ...res.data.data]
        }
      })
    },
    onRefresh () {
      this.isLoading = true
      axios.get('/api/pro').then((res) => {
        this.isLoading = false
        this.prolist = res.data.data
        this.pageCode = 1
        this.finished = false
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/lib/reset.scss';
.box {
  .content {
    .searchbox {
      z-index: 9999;
      height: 0.54rem;
      width: 100%;
      position: fixed;
      top: 0;
      form {
        width: 100%;
      }
    }
    .position-box {
      height: 0.54rem;
      width: 100%;
    }
    .van-nav-bar {
    }
    .van-tabs {
    }
  }
  // 回到顶部和购物车
  .backtop {
    width: .4rem;
    height: 1rem;
    position: fixed;
    right: .08rem;
    bottom: .28rem;
    .icons {
      width: 0.32rem;
      height: 0.32rem;
      display: flex;
      justify-content: center;
      align-content: center;
      border: 2px solid #ccc;
      border-radius: 50%;
      background: #fff;
      margin-bottom: .05rem;
      .van-icon {
        margin: 0 auto;
      }
    }
  }
}
</style>
